import React from 'react';
import ReactDom from 'react-dom';
import Dropdown from '../index';

class DropdownBasicDemo extends React.Component {
  render() {
    let options = [
      {
        name: 'swift',
        value: 101
      }, {
        name: 'java',
        value: 121
      }, {
        name: 'javascript',
        value: 332
      }
    ];
    return (
      <div>
        <Dropdown
          options={options} defaultOption={options[0]} onChange={this.handleChange}
          style={{ width: '200px' }}
        />
        <h3>hover</h3>
        <Dropdown
          options={options}
          style={{ width: '200px' }}
          hoverable
        />
      </div>
    );
  }

  handleChange(newValue, oldValue) {
    console.log(newValue, oldValue);
  }
}

ReactDom.render(<DropdownBasicDemo />, document.getElementById('basic'));
